
@use '@angular/material' as mat;
@include mat.core();@import "../node_modules/@angular/material/theming";
// Fonts
@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500");
@include mat-core();
@import "./variables";


$c-primary: (
  50: #F4FFF6,
  100: #BDFFDE,
  200: #89F8C7,
  300: #6CDBAC,
  400: #4EBF92,
  500: #2CA378,
  600: #008861,
  700: #006C4C,
  800: #005138,
  900: #003826,
  contrast: (
    50: #000000,
    100: #000000,
    200: #000000,
    300: #ffffff,
    400: #ffffff,
    500: #ffffff,
    600: #ffffff,
    700: #ffffff,
    800: #ffffff,
    900: #ffffff,
  ),
);

$c-accent: (
  50:#F4FFF6,
  100: #DDF7E7,
  200: #CFE9D9,
  300: #B3CCBE,
  400: #98B1A3,
  500: #7E9689,
  600: #657C70,
  700 :#4D6357,
  800 :#354B40,
  900 :#1F352A,
  contrast: (
    50: #000000,
    100: #000000,
    200: #000000,
    300: #ffffff,
    400: #ffffff,
    500: #ffffff,
    600: #ffffff,
    700: #ffffff,
    800: #ffffff,
    900: #ffffff,
  ),
); 


//font-size,line-height,font-weight
$custom-typograpy: mat-typography-config(
  $font-family: 'Roboto',
  $display-1: mat-typography-level(48px, 56px, 400),
  $display-2: mat-typography-level(34px, 36px, 400),
  $display-3: mat-typography-level(24px, 24px, 400),
  $display-4: mat-typography-level(20px, 24px, 400),
  $subheading-1: mat-typography-level(16px, 24px, 400),
  $subheading-2: mat-typography-level(14px, 24px, 500),
  $body-2: mat-typography-level(14px, 20px, 400),
  $body-1: mat-typography-level(16px, 24px, 400),
  $caption: mat-typography-level(12px, 16px, 400),
  $button: mat-typography-level(14px, 16px, 500),
);
@include mat-core($custom-typograpy);

// Both required
// 1) pallet (required) 2) default color 3) light color 4) dark color
//$custom-primary: mat-palette($mat-light-green, 600);
//$custom-accent:  mat-palette($mat-amber, 100);
$custom-primary: mat-palette($c-primary, 700,200,700);
$custom-accent: mat-palette($c-accent, 500,200,700);


$custom-theme: mat-light-theme($custom-primary, $custom-accent);
@include angular-material-theme($custom-theme);
@mixin mix-app-theme($custom-theme) {
  $primary: map-get($custom-theme, primary);
  $accent: map-get($custom-theme, accent);
  // $warn: map-get($custom-theme, warn);

  .mat-button-toggle {
    background-color: #636363;
    color: #ffffff;
  }

  .mat-focused .mat-form-field-required-marker { color: #00593C}

  .mat-button-toggle-checked {
    background-color: mat-color($accent);
    color: mat-color($accent, default-contrast);
  }
  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type="number"] {
    -moz-appearance: textfield;
  }
}
.bold {
  font-weight: 500;
}

// Include the mixin
@include mix-app-theme($custom-theme);

mat-card {
  @include mat.elevation(8);
}





