/* You can add global styles to this file, and also import other style files */
html, body {
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.material-symbols-outlined {
  font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 48;
}

.spacer {
  display: flex;
  flex: 1 1 auto;
}

.error-hint {
  color: red;
}

.example-full-width {
  width: 90%;
}

.text-87 {
  color: rgba(0, 0, 0, 0.87);
}

.text-60 {
  color: rgba(0, 0, 0, 0.6);
}

.p-0 {
  padding: 0 !important;
}

.m-0 {
  margin: 0 !important;
}

.pt-8 {
  padding-top: 8px;
}

.mt-10 {
  margin-top: 10px;
}

.mat-list-item-content {
  padding: 0 0 0 16px !important;
}

.mat-list-text {
  padding: 0 8px 0 0 !important;
}

.mat-subheading-1 {
  margin: 0px !important;
}

.mat-subheading-2 {
  margin: 0px !important;
}

.mat-display-4 {
  margin: 0px !important;
}

.mat-display-3 {
  margin: 0px !important;
}

.mat-display-2 {
  margin: 0px !important;
}

.mat-display-1 {
  margin: 0px !important;
}

.mat-raised-button {
  background-color: var(--primary-700);
  color: white;
}

.mat-radio-button.mat-accent.mat-radio-checked .mat-radio-outer-circle {
  border-color: #006C4C !important;
}

.mat-radio-button.mat-accent .mat-radio-inner-circle {
  background: #006C4C !important;
}

.mat-checkbox-checked.mat-accent .mat-checkbox-ripple .mat-ripple-element {
  background-color: #006C4C !important;
}

.mat-checkbox-checked.mat-accent .mat-checkbox-background, .mat-checkbox-indeterminate.mat-accent .mat-checkbox-background {
  background-color: #006C4C !important;
}

.mat-pseudo-checkbox.mat-pseudo-checkbox.ng-star-inserted.mat-pseudo-checkbox-checked {
  background-color: #006C4C !important;
}

.mat-form-field-appearance-outline .mat-form-field-prefix,
.mat-form-field-appearance-outline .mat-form-field-suffix {
  top: 0 !important;
  color: rgba(12, 12, 12, 0.6);
}

.bg-color .mat-dialog-container {
  background-color: #DDEBE3 !important;
}

/* Works on Chrome, Edge, and Safari */
/* Works on Firefox */
@supports not selector(::-webkit-scrollbar) {
  html {
    scrollbar-width: thin;
    scrollbar-color: #003826 #003826;
  }
}
*::-webkit-scrollbar-track {
  background: rgb(255, 255, 255);
}

*::-webkit-scrollbar-thumb {
  background-color: #003826;
  border-radius: 20px;
  border: 3px solid #003826;
}

.add-user-dialog .mat-dialog-container {
  background: var(--primary-50) !important;
}